@import url('https://fonts.googleapis.com/css2?family=Bellota+Text:wght@300;400;700&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Bellota Text', cursive;
  background: #151515;
}

.loader {
  display: block;
  height: 100vh;
  background-color: #151515;
  z-index: 2000;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.percent {
  color: #999;
  font-size: 100px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 15px;
}

.progress-bar {
  width: 406px;
  height: 21px;
  background: #111;
  border-radius: 13px;
  padding: 3px;
  box-sizing: border-box;
}

.progress {
  width: 10px;
  height: 15px;
  background: #3feb98;
  border-radius: 13px;
}

.text {
  position: absolute;
  left: 160px;
  color: #b8ffdc;
  font-size: 15px;
  letter-spacing: 1px;
  line-height: 21px;
  display: none;
}

.text-blink {
  animation: animate 0.7s ease-in-out 0.1s;
}

@keyframes animate {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}
